import { clear } from '@testing-library/user-event/dist/clear';
import React,{useState} from 'react'

export default function TextForm(props) {
    const handleUpClick = ()=>{        //its is method
        // console.log("Uppercase was clicked"+ text);
        let newtext = text.toUpperCase();
        setText(newtext)
        props.showAlert("Converted to uppercase!", "success");
    }
     const handleLoClick = ()=>{        //its is method
        // console.log("Uppercase was clicked"+ text);
        let newtext = text.toLowerCase();
        setText(newtext)
         props.showAlert("Converted to Lowercase!", "success");
    }
     const handleDownloadClick = () => {
    console.log("Download was clicked. Current text: " + text);
    const blob = new Blob([text], { type: "text/plain" });
    const link = document.createElement("a");
    link.href = URL.createObjectURL(blob);
    link.download = "myText.txt";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
   const handleClear = ()=>{        //its is method
        // console.log("Uppercase was clicked"+ text);
        let newtext = '';
        setText(newtext)
        props.showAlert("Text Cleared!", "success");
    }
    const handleExtraSpaces =()=>{
      let newText = text.split(/[ ]+/);
      setText(newText.join(" "))
      props.showAlert("Extra space!", "success");
    }

    const handleOnChange = (event)=>{
         console.log("On Change");
        setText(event.target.value);
    }
    const [text, setText] = useState('');
    // text = "neext text"; wrong way to change the text
    // setText("new text");
  return (
    <>
    <div className ="container" style={{color:props.mode ==='dark'?'white':'#042743'}}> 
        <h1>{props.heading}</h1>
         <div className="mb-3">
         <textarea className="form-control" value={text}  onChange={handleOnChange} style ={{backgroundColor:props.mode ==='dark'?'grey':'white', color:props.mode ==='#042743'?'white':'black'}}id="myBox" rows="10"></textarea>
         </div>
         <button className="btn btn-primary mx-2" onClick={handleUpClick}>Convert to Uppercase</button>
         <button className="btn btn-warning mx-2" onClick={handleLoClick}>Convert to Lowercase</button>
         <button className="btn btn-success mx-2" onClick={handleDownloadClick}>Download Text</button>
         <button className="btn btn-danger mx-2" onClick={handleClear}>Clear Text</button>
         <button className="btn btn-primary mx-2" onClick={handleExtraSpaces}>Remove Extra</button>
    </div>
    <div className="container my-3" style={{color:props.mode==='dark'?'white':'#042743'}}>
      <h2>Your text summary</h2>
      <p> {text.split(" ").length}words and{text.length} characters</p>
      <p>{0.008*text.split("").length}Minutes read</p>
      <h2>Preview</h2>
      <p>{text.length>0?text:"Enter something in the textto preview it here"}</p>
    </div>
    </>
   
  )
}
